<template>
  <div :class="['navbar-burger-menu', isMenuOpen ? 'is-active' : '']">
    <div class="navbar-burger-menu-close" @click="closeMenu"></div>
    <nav>
      <router-link v-for="(page, index) in pages" :key="index" to="">
        {{ page }}
      </router-link>
    </nav>
  </div>
</template>

<script>
  export default {
    name: "NavbarBurgerMenu",
    props: {
      isMenuOpen: {
        type: Boolean,
        default: false,
      },
    },
    setup(props, { emit }) {
      const pages = ["ПАРКОВКА", "СТРАХОВКА", "БЕНЗИН", "ОБСЛУЖИВАНИЕ"];

      function closeMenu() {
        emit("update:isMenuOpen", false);
      }

      return { pages, closeMenu };
    },
  };
</script>

<style scoped lang="scss">
  .navbar-burger-menu {
    display: none;
    opacity: 0;
  }
  .navbar-burger-menu.is-active {
    display: flex;
    position: absolute;
    z-index: 3;
    left: 0;
    top: 0;
    align-items: center;
    width: 100%;
    height: 100%;
    background: rgba(21, 27, 31, 0.81);
    opacity: 1;
    transition: opacity 1s ease-in;

    & nav {
      display: flex;
      flex-direction: column;
      padding-left: 128px;
      align-items: flex-start;
      box-sizing: border-box;
      height: 100%;
      justify-content: center;
      background: #111518;
      max-width: calc(50% + 33px);
      width: calc(50% + 33px);
    }

    & nav a {
      font-weight: 500;
      font-size: 32px;
      color: $white;
      margin-bottom: 10px;

      &:hover {
        color: $green;
      }
    }
  }

  .navbar-burger-menu-close {
    position: absolute;
    top: 32px;
    left: 16px;
    width: 32px;
    height: 32px;
    cursor: pointer;

    &:before,
    &:after {
      position: absolute;
      content: "";
      width: 3px;
      height: 16px;
      background: $white;
      transform: rotate(0);
    }

    &:before {
      transform: rotate(45deg);
      transition: rotate 0.3s ease;
    }

    &:after {
      transform: rotate(-45deg);
      transition: rotate 0.3s ease;
    }
  }
</style>
